@property --a {
  syntax: "<angle>";
  inherits: false;
  initial-value: 0deg;
}

@keyframes rotating {
  0% {
    --a: 0deg;
  }

  100% {
    --a: 360deg;
  }
}

.el-progress-bar__outer {
  height: 24px;
  border-radius: 100px;
  background-color: var(--el-border-color-lighter);
  overflow: hidden;
  position: relative;
  vertical-align: middle;
}

.el-progress-bar__inner--striped.el-progress-bar__inner--striped-flow {
  animation: striped-flow 3s linear infinite;
  width: 57.98%;
  animation-duration: 10s;
}

.el-progress-bar__inner--striped {
  background-image: linear-gradient(
    45deg,
    rgba(0, 0, 0, 0.1) 25%,
    transparent 25%,
    transparent 50%,
    rgba(0, 0, 0, 0.1) 50%,
    rgba(0, 0, 0, 0.1) 75%,
    transparent 75%,
    transparent
  );
  background-size: 1.25em 1.25em;
}

.el-progress-bar__inner {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  background-color: var(--el-color-primary);
  text-align: right;
  border-radius: 100px;
  line-height: 1;
  white-space: nowrap;
  transition: width 0.6s ease;
}

.el-progress-bar__inner {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  background-color: var(--el-color-primary);
  text-align: right;
  border-radius: 100px;
  line-height: 1;
  white-space: nowrap;
  transition: width 0.6s ease;
}

.el-progress-bar__innerText {
  display: inline-block;
  vertical-align: middle;
  color: #fff;
  font-size: 12px;
  margin: 0 5px;
}

.el-progress-bar__inner:after {
  display: inline-block;
  content: "";
  height: 100%;
  vertical-align: middle;
}

.pad-left {
  padding-left: 1.5rem !important;
}

.bg-herobtngrad {
  background: #67c319;
  border-color: #67c319;
}

.bg-herobtngrad:hover {
  background: transparent;
  color: #67c319;
}

body section > .max-w-4xl:before {
  content: "";
  position: absolute;
  inset: 0;
  background: repeating-conic-gradient(from var(--a), #67c319, #ff5447);
  border-radius: 20px;
  animation: rotating 6s linear infinite;
}

body section > .max-w-4xl:after {
  content: "";
  position: absolute;
  inset: 0;
  background: repeating-conic-gradient(from var(--a), #67c319, #ff5447);
  border-radius: 20px;
  animation: rotating 6s linear infinite;
  filter: blur(40px);
  opacity: 0.8;
}

.inner-about {
  padding: 10px;
  margin: 100px auto;
  position: relative;
}

.inner-about span {
  position: absolute;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  background: #222;
  border-radius: 16px;
  z-index: 1;
}

.inner-about video {
  width: 100%;
  border-radius: 16px;
}

div.reflection,
span.reflection {
  -webkit-transform: scaleY(-1);
  -moz-transform: scaleY(-1);
  -o-transform: scaleY(-1);
  -ms-transform: scaleY(-1);
  transform: scaleY(-1);
  position: absolute;
  left: 0;
  /* width: max-content; */
}

.reflection.color-gold {
  background-image: linear-gradient(
    0deg,
    #ffc319 0%,
    #ffc2193e 25%,
    transparent 50%
  );
  top: 65px;
}

.reflection.color-white {
  background-image: linear-gradient(
    0deg,
    white,
    #ffffff3e 25%,
    transparent 50%
  );
  background-clip: text;
  -webkit-text-fill-color: #0000;
  top: 65px;
}

.font-coiny .reflection {
  top: 28px;
}

.tokenomic-legend > div {
  height: 50px;
  align-content: center;
}

.lb-feature-subject {
  height: 56px;
  align-content: center;
}

.slider-track {
  display: flex;
  gap: 20px;
  animation: translate-x 40s linear infinite;
}

@keyframes translate-x {
  to {
    transform: translateX(-4000px);
  }
}

.slider {
  overflow: hidden;
}

.partners img {
  width: 100px;
  border-radius: 10px;
}

@media screen and (max-width: 768px) {
  .reflection {
    top: 38px !important;
  }

  .font-coiny .reflection {
    top: 18px !important;
  }

  .inner-about {
    padding: 5px;
  }
}
